*{
  margin: 0;
  padding: 0;
}
.app{
  text-align: center;
  color:yellow;
}

.app  input{
  font-size:14px;
  border-radius:5px;
  border:1px solid gray;
  outline: none;
  padding:10px 5px ;
  width:200px ;

}

button{
  background-color:teal;
  color:wheat;
  border: none;
  outline:none;
  margin-top: 10px;
  padding: 10px 30px;
  font-size: 16px;
  cursor: pointer;

}

nav{
  display: flex;
  justify-content: center;
  margin-top: 5px;
  background-color:springgreen;
  padding: 10px 0;
}

nav ul{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 40%;
  color:beige;
  font-weight: bold;
  cursor: pointer;
  transition:all 0.5s ease-in-out;
}

nav ul li:hover{
  transform: scale(1.2);
}

.navActive{
  color:salmon;
  font-size:30px;
  font-weight: bold;
}

.app-container{

  background-color: aqua;
  color: black;
  padding:10px;
}